
<template>
    <div style="margin-top:10px; padding-bottom: 20px; background: white">
        <div class="title">历任基金收益情况</div>
        <div class="hfp-tools">
            <div style="font-size: 12px;line-height: 16px">Zoom</div>
            <el-radio-group v-model="zoom" class="zooms">
                <el-radio-button label="1" border>1月</el-radio-button>
                <el-radio-button label="3" border>3月</el-radio-button>
                <el-radio-button label="6" border>6月</el-radio-button>
                <el-radio-button label="12" border>1年</el-radio-button>
                <el-radio-button label="0" border>全部</el-radio-button>
            </el-radio-group>
            <div style="flex: 1 1 auto"></div>
            <div class="date-label">From</div>
            <el-date-picker
                v-model="fromDate"
                align="right"
                type="date"
                placeholder="选择日期"
                @change="change"
                :picker-options="pickerOptions">
            </el-date-picker>
            <div class="date-label">To</div>
            <el-date-picker
                v-model="toDate"
                align="right"
                type="date"
                placeholder="选择日期"
                @change="change"
                :picker-options="pickerOptions">
            </el-date-picker>
        </div>
        <v-chart ref="chart"
                 :options="options"
                 @datazoom="onDataZoom"
                 style="width: 100%;height:300px;padding-bottom: 15px" />
    </div>

</template>
<script>
  import { get,post }  from '../../common/function/service';
  import VCharts from 'vue-echarts'

  function formateDate(now) {
    var texts = [now.getFullYear(), ("0" + (now.getMonth() + 1)).substr(-2)];
    return texts.join('-');
  }

  export default {
    data() {
      let year=2000;
      let month = 0
      let date = [];

      let data = [Math.random() * 300];

      for (let i = 1; i < 240; i++) {
        let now = new Date(year, month++, 1);
        if(month===12){
          month = 0;
          year ++;
        } ;
        var texts = formateDate(now);
        date.push(texts);
        data.push(Math.round((Math.random() - 0.5) * 20 + data[i - 1]));
      }

      var options = {
        grid: {
          bottom: '100px',
        },
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%'];
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: date,
          axisLabel: {
            rotate: 45,
          },

        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%']
        },
        dataZoom: [ {
          start: 0,
          end: 100,
          handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
          handleSize: '80%',
          handleStyle: {
            color: '#fff',
            shadowBlur: 3,
            shadowColor: 'rgba(0, 0, 0, 0.6)',
            shadowOffsetX: 2,
            shadowOffsetY: 2,
          },
          textStyle:{
            color:'#000000',
            fontSize:'16'
          }
        }],
        series: [
          {
            name: '基金收益',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              color: 'rgb(255, 70, 131)'
            },
            lineStyle: {
              color: '#72ACD1',
            },
            data: data
          }
        ]
      };
      let firstDate = new Date(date[0]);
      let lastDate = new Date(date[date.length-1]);

      return  {
        options,
        fromDate: firstDate,
        toDate: lastDate,
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > lastDate.getTime()
              || time.getTime() < firstDate.getTime();
          },
        },
        zoom:'0'
      };
    },
    created() {

    },
    computed: {

    },
    methods: {
      change(){
        const {fromDate,toDate} = this;
        if(fromDate && toDate){
          if(fromDate.getTime() >= toDate.getTime()) {
            this.$message.error("选择的时间段有误");
            return;
          }
          this.$refs.chart.dispatchAction({
            type: 'dataZoom',
            dataZoomIndex: 0,
            startValue: formateDate(this.fromDate),
            endValue: formateDate(this.toDate)
          })

        }
      },
      onDataZoom(param){
        let date = this.options.xAxis.data;
        const {start,end} = param;
        if (!start || !end) return;
        const total = date.length-1;
        this.fromDate = new Date(date[Math.round(total * start * 0.01)]);
        this.toDate = new Date(date[Math.round(total * end * 0.01)]);
      }

    },
    components: {
      'v-chart':VCharts
    },
  }
</script>
