<script>
  import { get,post }  from '../../common/function/service';
  import BasicInfo  from './basicInfo';
  import CapacityLevel  from './capacityLevel';
  import HistoryFunds  from './historyFunds';
  import HistoryFundProfit  from './historyFundProfit';
  import Styles  from './styles';
  import Capcities from './capcities'
  import Profits from './profits'


  export default {
      data() {
          return {
            params: '',
            currentActive: "1",
            funds: [
              {
                code: "00011",
                name: '基金名称1'
              },
              {
                code: '00012',
                name: '基金名称2'
              }
            ]
          }
      },
      created() {
        this.$store.state.hideTopMenu = true;
        this.currentActive =  "1";

        this.params = this.$route.params;
        let code = this.params.code;
        window.scrollTo({
          top: 0,
        });
        window.addEventListener('scroll', this.onScroll)
      },
      destroyed() {
        window.removeEventListener('scroll', this.onScroll);

      },
      computed: {

      },

      methods: {
        onSelect(key, keyPath) {
          let dom = this.$refs["menu"+key];
          let top = key==1 ? 0 : dom.$el.getBoundingClientRect().top + window.scrollY;
          window.scrollTo({
            top: top,
            behavior: "smooth"
          });
        },
        onScroll(){
          let index = 1;
          for(let i = 7; i > 0; i -- ){
            let dom = this.$refs["menu"+i];
            if(dom.$el.getBoundingClientRect().top<0){
              index = Math.min(7,(i+1));
              break;
            }
          }
          this.currentActive = index.toString();
        }
      },
      components: {
        BasicInfo: BasicInfo,
        CapacityLevel: CapacityLevel,
        HistoryFunds: HistoryFunds,
        HistoryFundProfit: HistoryFundProfit,
        Styles: Styles,
        Capcities: Capcities,
        Profits:Profits
      },
    }
</script>
